import React, { useContext } from 'react';
import WordleContext from '../context/WordleContext';
import './HowToPlay.css';

export default function HowToPlay() {
  const { instructions: { setShowHowToPlay } } = useContext(WordleContext);

  return (
    <div className="containerHowToPlay">
      <section>
        <div className="containerClose">
          <button
            type="button"
            onClick={ () => setShowHowToPlay(false)
          }>X</button>
        </div>
        <h2>Como jogar?</h2>
        <p>
          A cada novo jogo, uma nova palavra aparecer no WORDAY para voc adivinhar.
          Voc ter 6 tentativas. Cada uma delas deve ser uma palavra que exista.
          Acentos e cedilha so ignorados, tanto nas tentativas, quanto na resposta.
          Aps chutar, as letras mudaro para indicar o quo perto voc est da resposta.
        </p>
        <ul>
          <li>Se uma letra ficar verde, ela est presente na palavra e na posio correta.</li>
          <li>Se uma letra ficar amarela, ela est presente na palavra, mas na posio errada.</li>
          <li>Se uma letra ficar vermelha, ela NO est na palavra.</li>
        </ul>
        <div className="containerLetters">
          <div className="boxLetter letterIsInTheRightPlace">T</div>
          <div className="boxLetter">E</div>
          <div className="boxLetter letterExistInWord">S</div>
          <div className="boxLetter">T</div>
          <div className="boxLetter letterNotExistInWord">E</div>
        </div>
        <p>Desenvolvido por <a href="https://www.linkedin.com/in/vitormoutim/">Vitor Moutim</a></p>
      </section>
    </div>
  )
}
